// Result.js
import React, {useState} from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import {
  selectStockTicker,
  selectDays,
  selectExitLevel,
  selectCombinedProfit,
  selectCostPerShares,
  selectROR,
  selectBreakEven,
  selectDownsideProtection,
  selectAnnualizedROR,
} from '../../store/selectors';
import TradingViewWidget from '../TradingView';
import './styles.css';
import { isValidNumber, formatNumber } from '../../helpers'
import ProgressBar from '../ProgressBar';
const Result = () => {
  const [currentStep, setCurrentStep] = useState(2);

  const steps = [
    { label: '1000' },
    { label: '2000', hasChanges: true },
    { label: '3000', hasChanges: true },
    { label: 'one million' },
    { label: 'three hundred backs', hasChanges: true },
    { label: 'hello' },
    { label: 'Test' },
    { label: 'Step' },
    { label: 'Step' },
    { label: 'test' },
  ];

  const stockTicker = useSelector(selectStockTicker);
  const days = useSelector(selectDays);
  const exitLevel = useSelector(selectExitLevel);
  const combinedProfit = useSelector(selectCombinedProfit);
  const costPerShares = useSelector(selectCostPerShares);
  const ror = useSelector(selectROR);
  const breakEven = useSelector(selectBreakEven);
  const downsideProtection = useSelector(selectDownsideProtection);
  const annualizedROR = useSelector(selectAnnualizedROR);
  // const stockPrice = useSelector(selectStockPrice);

const isAnnualizedROR = isValidNumber(parseFloat(annualizedROR)) ? annualizedROR : 0;
const isROR = isValidNumber(parseFloat(ror)) ? ror : 0;
const isCombinedProfit = isValidNumber(combinedProfit) ? combinedProfit : 0;
const isDownsideProtection= isValidNumber(downsideProtection) ? downsideProtection : 0;
const formatedROR = formatNumber.format(isROR);
const costPerSharesData = formatNumber.format(costPerShares);
const combinedNetOutput = formatNumber.format(isCombinedProfit);
const annualizedROROutput = formatNumber.format(isAnnualizedROR);
const downsideProtectionOutput = isDownsideProtection.toLocaleString('en-US', { style: 'percent', maximumFractionDigits: 2 });

  return (
    <div className="result-container">
  <div className="title-and-content-wrapper">
    <h2 className="result-title">Your Results</h2>
    <div className="content-wrapper">
      <div className="result-grid">
<div className="result-item">
               <span className="result-item-title">Stock Ticker</span>
              <span className="result-item-value">{stockTicker}</span>
             </div>
             <div className="result-item">
               <span className="result-item-title">Days</span>
               <span className="result-item-value">{days}</span>
             </div>
             <div className="result-item">
               <span className="result-item-title">Exit Level</span>
               <span className="result-item-value">${exitLevel}</span>
             </div>
            <div className="result-item">
             <span className="result-item-title">
               Combined Net Credit / Profit
            </span>
            <span className="result-item-value">${combinedNetOutput}</span>
          </div>
          <div className="result-item">
            <span className="result-item-title">Cost per 100 shares</span>
            <span className="result-item-value">${costPerSharesData}</span>
          </div>
          <div className="result-item">
            <span className="result-item-title">ROR</span>
           <span className="result-item-value">{formatedROR}%</span>
            </div>
             <div className="result-item">
               <span className="result-item-title">Downside protection</span>
               <span className="result-item-value">{downsideProtectionOutput}</span>
             </div>
             <div className="result-item">
              <span className="result-item-title">Annualized ROR</span>
               <span className="result-item-value">{annualizedROROutput}%</span>
            </div>
             <div className="result-item">
              <span className="result-item-title">Break Even</span>
               <span className="result-item-value">{breakEven}</span>
             </div>
      </div>
    </div>
  </div>
  <div className="cover-call-container">
    <h3>The Covered Call</h3>
    <div className="result-item">
      <span className="result-item-title">Cost Basis</span>
      <span className="result-item-value">0</span>
    </div>
    <div className="result-item">
      <span className="result-item-title">Break Even</span>
      <span className="result-item-value">0</span>
    </div>
    <ProgressBar currentStep={currentStep} steps={steps} />
    <div className="tradingview-container">
      <h3 className="tradingview-title">Interactive Chart</h3>
      <TradingViewWidget stockTicker={stockTicker} />
    </div>
  </div>
</div>
  );
};

Result.propTypes = {
  stockTicker: PropTypes.string,
  days: PropTypes.number,
  exitLevel: PropTypes.number,
  combinedProfit: PropTypes.number,
  costPerShares: PropTypes.number,
  ror: PropTypes.number,
  breakEven: PropTypes.number,
  downsideProtection: PropTypes.number,
  annualizedROR: PropTypes.number,
};

export default Result;
