import { createSelector } from '@reduxjs/toolkit';

export const getStockTicker = (state) => state.formData.stockTicker;
export const getDays = (state) => state.formData.days;
export const getExitLvl = (state) => state.formData.exitLevel;
export const getCombinedProfit = (state) => state.formData.combinedProfit;
export const getCostPerShares = (state) => state.formData.costPerShares;
export const getRORState = (state) => state.formData.ror;
export const getBreakEven = (state) => state.formData.breakEven;
export const getAnnualizedROR = (state) => state.formData.annualizedROR;
export const getDownsideProtection = (state) => state.formData.downsideProtection;

// Memoized selectors
export const selectExpirationDate = createSelector(
  (state) => state.formData.expirationDate,
  (expirationDate) => expirationDate
);

export const selectEntryDate = createSelector(
  (state) => state.formData.entryDate,
  (entryDate) => entryDate
);

export const selectStockTicker = createSelector(getStockTicker, (stockTicker) => stockTicker);
export const selectStockPrice = createSelector((state) => state.formData.stockPrice, (stockPrice) => stockPrice);
export const selectShortStrike = createSelector((state) => state.formData.shortStrike, (shortStrike) => shortStrike);
export const selectCredit = createSelector((state) => state.formData.credit, (credit) => credit);
export const selectDividend = createSelector((state) => state.formData.dividend, (dividend) => dividend);
export const selectCombinedProfit = createSelector(getCombinedProfit, (combinedProfit) => combinedProfit);
export const selectCostPerShares = createSelector(getCostPerShares, (costPerShares) => costPerShares);
export const selectROR = createSelector(getRORState, (ror) => ror);
export const selectDays = createSelector(getDays, (days) => days);
export const selectExitLevel = createSelector(getExitLvl, (exitLevel) => exitLevel);
export const selectBreakEven = createSelector(getBreakEven, (breakEven) => breakEven);
export const selectAnnualizedROR = createSelector(getAnnualizedROR, (annualizedROR) => annualizedROR);
export const selectDownsideProtection = createSelector(getDownsideProtection, (downsideProtection) => downsideProtection);
