import React, { useEffect } from 'react';
import { Formik, Form, Field } from 'formik';
import CustomInput from '../CustomInput';
import './styles.css';
import { useDispatch, useSelector } from 'react-redux';
import {
  selectExpirationDate,
  selectEntryDate,
  selectStockTicker,
  selectStockPrice,
  selectShortStrike,
  selectCredit,
  selectDividend,
  selectCombinedProfit,
  selectCostPerShares,
  selectBreakEven,
  selectROR,
  selectDays,
} from '../../store/selectors'
import {
  setStockTicker,
  setEntryDate,
  setUpdateDays,
  setExpirationDate,
  setExitLevel,
  setStockPrice,
  setCredit,
  setDividend,
  setCombinedProfit,
  setShortStrike,
  setCostPerShares,
  setROR,
  setBreakEven,
  setDownsideProtection,
  setAnnualizedROR,
} from '../../store/formatDataSlice';
import * as yup from 'yup';
import { formatNumber } from '../../helpers';


const validationSchema = yup.object().shape({
  stockTicker: yup.string().required('Stock ticker is required'),
  entryDate: yup
    .date()
    .required('Entry date is required')
    .max(yup.ref('expirationDate'), 'Entry date must be before expiration date'),
  expirationDate: yup
    .date()
    .required('Expiration date is required')
    .min(yup.ref('entryDate'), 'Expiration date must be after entry date'),
  // stockPrice: yup.number().required('Stock price is required'),
  stockPrice: yup.number().min(0.01, 'Stock price must be greater than 0').required('Stock price is required'),
  shortStrike: yup.number().required('Short strike is required'),
  credit: yup.number().required('Credit is required'),
  dividend: yup.number().required('Dividend is required'),
});
const getTodayDate = () => {
  const today = new Date();
  const yyyy = today.getFullYear();
  const mm = String(today.getMonth() + 1).padStart(2, '0'); // Months are 0-indexed
  const dd = String(today.getDate()).padStart(2, '0');

  return `${yyyy}-${mm}-${dd}`;
};

const MyForm = () => {
  const today = getTodayDate();
  const dispatch = useDispatch();
  const expirationDate = useSelector(selectExpirationDate);
  const entryDate = useSelector(selectEntryDate);
  const stockTicker = useSelector(selectStockTicker);
  const stockPrice = useSelector(selectStockPrice);
  const shortStrike = useSelector(selectShortStrike);
  const credit = useSelector(selectCredit);
  const dividend = useSelector(selectDividend);
  const combinedProfit = useSelector(selectCombinedProfit);
  const costPerShares = useSelector(selectCostPerShares);
  const breakEven = useSelector(selectBreakEven);
  const ror = useSelector(selectROR);
  const days = useSelector(selectDays);

  const calculateDays = (entryDateString, expirationDateString) => {
    const entryDate = new Date(entryDateString);
    const expirationDate = new Date(expirationDateString);
    const oneDayMilliseconds = 1000 * 60 * 60 * 24;
    const days = (expirationDate - entryDate) / oneDayMilliseconds;
    return Math.ceil(days);
  };

useEffect(() => {
  const days = calculateDays(entryDate, expirationDate);
  if (!isNaN(days) && days >= 0) {
    dispatch(setUpdateDays(days));
  }

  if (!isNaN(stockPrice)) {
    const stockExitLevel = Math.round(stockPrice * 0.92);
    dispatch(setExitLevel(stockExitLevel));

    const costPerShares = (parseFloat(stockPrice - credit) * 100).toFixed(2);
    dispatch(setCostPerShares(costPerShares));

    const breakEvenData = parseFloat(stockPrice - (credit - dividend)).toFixed(2);
    dispatch(setBreakEven(breakEvenData));

    if (breakEven > 0 && !isNaN(breakEven)) {
      const downsideProtectionData = ((breakEven / stockPrice) - 1);
      dispatch(setDownsideProtection(downsideProtectionData));
    }
  }

  const combinedNet =
    parseFloat(credit) +
    parseFloat(dividend) -
    parseFloat(stockPrice - shortStrike);
  dispatch(setCombinedProfit(combinedNet));

  const annualizedRORData = (ror / days) * 365;
  const outputAnnRORData = annualizedRORData.toFixed(2) 
  dispatch(setAnnualizedROR(outputAnnRORData));

  const rorData = parseFloat((combinedProfit * 100) / costPerShares);
  const rorOutPut = (rorData * 100).toFixed(2);
  dispatch(setROR(rorOutPut));

}, [
  entryDate,
  expirationDate,
  stockPrice,
  shortStrike,
  credit,
  dividend,
  breakEven,
  combinedProfit,
  costPerShares,
  ror,
  days,
  dispatch
]);

  const handleSetCredit = (e, formikProps) => {
    const value = e.target.value
    formikProps.setFieldValue('credit', value)
    dispatch(setCredit(value));
  };

  const handleSetDividend = (e, formikProps) => {
    const value = e.target.value
    formikProps.setFieldValue('dividend', value)
    dispatch(setDividend(value));
  };

  const handleStockTickerChange = (e, formikProps) => {
    const value = e.target.value
    formikProps.setFieldValue('stockTicker', value)
    dispatch(setStockTicker(value));
  };

  const handleEntryDateChange = (e, formikProps) => {
    const value = e.target.value
    formikProps.setFieldValue('entryDate', value)
    dispatch(setEntryDate(value));
  };

  const handleExpirationDateChange = (e, formikProps) => {
    const value = e.target.value
    formikProps.setFieldValue('expirationDate', value)
    dispatch(setExpirationDate(value));
  };

 const handleStockPriceChange = (e, formikProps) => {
  const value = e.target.value === '' ? '' : Number(e.target.value);
  formikProps.setFieldValue('stockPrice', value);
  dispatch(setStockPrice(value));
};

  const handleShortStrikeChange = (e, formikProps) => {
    const value = e.target.value === '' ? '' : Number(e.target.value);
    formikProps.setFieldValue('shortStrike', value)
    dispatch(setShortStrike(value));
  };


  return (
    <div>
      <h1 className='main-title'>Steady Calculator</h1>
      <Formik
        initialValues={{
          stockTicker: 'AAPL',
          entryDate: today,
          expirationDate: '',
          stockPrice: '',
          shortStrike: '',
          credit: '',
          dividend: '',
        }}
        validationSchema={validationSchema}
      >
        {(formikProps) => (
          <Form>
            <div className="form-grid">
              <Field
                component={CustomInput}
                label="Enter stock/ticker"
                name="stockTicker"
                onChange={(e) => handleStockTickerChange(e, formikProps)}
                placeholder="e.g AAPL"
                value={stockTicker}
                formikProps={formikProps}
              />
              <Field
                component={CustomInput}
                label="Entry date"
                name="entryDate"
                onChange={(e) => handleEntryDateChange(e, formikProps)}
                type="date"
                value={entryDate}
                formikProps={formikProps}
              />
              <Field
                component={CustomInput}
                label="Expiration date"
                name="expirationDate"
                onChange={(e) => handleExpirationDateChange(e, formikProps)}
                type="date"
                formikProps={formikProps}
              />

              <Field
                component={CustomInput}
                label="Stock Price"
                name="stockPrice"
                onChange={(e) => handleStockPriceChange(e, formikProps)}
                type="number"
                formikProps={formikProps}
              />
              <Field
                component={CustomInput}
                label="Short strike"
                name="shortStrike"
                onChange={(e) => handleShortStrikeChange(e, formikProps)}
                type="number"
                formikProps={formikProps}
              />
              <Field
                component={CustomInput}
                label="Credit"
                name="credit"
                type="number"
                onChange={(e) => handleSetCredit(e, formikProps)}
                placeholder="enter credit number"
                formikProps={formikProps}
              />
              <Field
                component={CustomInput}
                label="Dividend"
                name="dividend"
                type="number"
                onChange={(e) => handleSetDividend(e, formikProps)}
                placeholder="eneter dividend number"
                formikProps={formikProps}
              />
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default MyForm;
